<template>
  <div class="base-modal" v-if="modal">
    <div class="base-modal-wrapper">
      <div
        class="base-modal-content"
        :class="{ open: showModal }"
        v-click-outside="close"
      >
        <div class="base-modal__close" @click="close">
          <img src="@/assets/icons/clear.svg" alt="Remove" />
        </div>
        <div class="base-modal-sent">
          <div class="base-modal__icon" :class="{ show: showModal }">
            <img src="@/assets/icons/check-mark-big.svg" alt="Accept" />
          </div>
          <span class="base-modal__text"> {{ text }} </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, watchEffect } from "vue";
export default {
  props: {
    open: {
      type: Boolean,
      required: true,
    },
    text: {
      type: String,
      default: "Отправлено",
    },
  },
  setup(props, { emit }) {
    let modal = ref(false);
    let showModal = ref(false);
    watchEffect(() => {
      if (props.open) {
        modal.value = true;
        document.body.style.overflow = "hidden";
        setTimeout(() => {
          showModal.value = true;
        }, 100);
      } else {
        showModal.value = false;
        setTimeout(() => {
          modal.value = false;
          document.body.style.overflow = "";
        }, 400);
      }
    });

    let close = () => {
      showModal.value && emit("close");
    };

    return {
      showModal,
      modal,
      close,
    };
  },
  emits: ["close", "modalSubmit"],
};
</script>

