    padding: 64px 0;
    background: rgba($black, 0.5);
  }
  &-content {
    background: $white;
    border-radius: 4px;
    transform: scale(0);
    visibility: hidden;
    margin: auto;
    position: relative;
    transition: 0.3s ease;
    &.open {
      visibility: visible;
      transform: scale(1);
    }
  }
  &-sent {
    width: 266px;
    height: 165px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__icon {
    width: 62px;
    height: 62px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $primary;
    border-radius: 50%;
    padding-left: 4px;
    img {
      transform: scale(0);
      transition: 0.2s ease 0.2s;
    }
    &.show {
      img {
        transform: scale(1);
      }
    }
  }
  &__text {
    font-family: $regular;
    font-size: 16px;
    line-height: 23px;
    font-weight: 400;
    margin-top: 15px;
    text-align: center;
  }
  &__close {
    position: absolute;
    top: 8px;
    right: 8px;
    cursor: pointer;
    width: 24px;
    height: 24px;
    opacity: 0.4;
