<template>
  <div
    class="base-input"
    :class="{
      'base-input-error': message || error,
      'base-input-tableInput': tableInput,
      'base-input-row': label,
    }"
    ref="inputWrapper"
    v-click-outside="
      () => {
        suggestion = false;
      }
    "
  >
    <label :for="id" v-if="label" :style="labelStyle"> {{ label }}</label>
    <div class="input">
      <input
        ref="input"
        :class="{
          search,
          searchIcon,
          filled: modelValue,
          short,
          addProduct,
        }"
        :type="type"
        :id="id"
        :name="id"
        :required="required"
        :disabled="disabled"
        :readonly="readonly"
        :placeholder="placeholder"
        :value="modelValue"
        :minLength="minLength ? minLength : ''"
        :maxlength="maxLength ? maxLength : ''"
        @input="$emit('update:modelValue', $event.target.value), $emit('input')"
        @focus="$emit('focus'), (suggestion = true)"
        @blur="$emit('blur')"
        @click="$emit('click')"
        @keypress.enter="$emit('keypressEnter'), (suggestion = false)"
        @keypress="$emit('keypress'), openSuggestion()"
      />
      <span
        class="input-tableInput-value"
        v-if="tableInput && symbol && modelValue"
        @click="$refs.input.focus()"
      >
        <span>{{ modelValue }}</span> {{ symbol }}
      </span>
      <!-- search suggestion -->
      <div
        class="search-suggestion"
        :style="[suggestionDirection, { width: `${suggestionWidth}px` }]"
        v-if="search && suggestions.length && suggestion"
        ref="suggestion"
      >
        <span
          v-for="(s, i) in suggestions"
          :key="i"
          @click="
            $emit('update:modelValue', s.key),
              $emit('setSuggestion', s.value),
              (suggestion = false)
          "
        >
          {{ s.key }}
        </span>
      </div>
      <!-- error message -->
      <span class="base-input__msg" v-if="message">
        {{ message }}
      </span>
      <!-- search icon -->
      <div class="base-input-icon" :class="{ clear: modelValue }" v-if="search">
        <img
          src="@/assets/icons/search-icon.svg"
          width="29"
          height="26"
          alt="Search"
          v-if="!modelValue && searchIcon"
        />
        <img
          v-else-if="modelValue"
          src="@/assets/icons/clear.svg"
          alt="Clear"
          width="23"
          height="23"
          @click="$emit('clear')"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "BaseInput",
  props: {
    type: {
      type: String,
      default: "text",
    },
    id: {
      type: String,
    },
    label: {
      type: String,
      default: "",
    },
    required: {
      type: Boolean,
    },
    message: {
      type: String,
      default: "",
    },
    error: {
      type: Boolean,
      default: false,
    },
    tableInput: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "",
    },
    modelValue: {
      type: [String, Number, null],
      required: true,
    },
    minLength: {
      type: Number,
    },
    maxLength: {
      type: Number,
    },
    disabled: {
      type: Boolean,
    },
    readonly: {
      type: Boolean,
    },
    search: {
      type: Boolean,
      default: false,
    },
    searchIcon: {
      type: Boolean,
      default: false,
    },
    suggestions: {
      type: Array,
    },
    suggestionWidth: {
      type: Number,
    },
    short: {
      type: Boolean,
    },
    labelStyle: {
      type: [Object, String],
    },
    addProduct: {
      type: Boolean,
    },
    symbol: {
      type: String,
    },
  },
  data() {
    return {
      suggestion: false,
      suggestionDirection: null,
    };
  },
  methods: {
    openSuggestion() {
      if (this.suggestions) this.suggestion = true;
      if (this.suggestion) {
        setTimeout(() => {
          let top = this.$refs.inputWrapper.getBoundingClientRect().top;
          if (window.innerHeight - top < 260)
            this.suggestionDirection = `top: auto; bottom: ${40}px`;
          else this.suggestionDirection = `top: ${40}px`;
        });
      }
    },
  },
  emits: [
    "update:modelValue",
    "clear",
    "input",
    "focus",
    "blur",
    "click",
    "keypressEnter",
    "keypress",
    "setSuggestion",
  ],
};
</script>

<style lang="scss" scoped>
.base-input {
  position: relative;
  .input {
    position: relative;
    height: 100%;
    &-tableInput-value {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      cursor: text;
      span {
        position: relative;
        color: transparent;
      }
    }
  }
  input {
    width: 100%;
    height: 40px;
    padding: 11px 24px 11px 11px;
    border: 1px solid $grayLighter;
    font-family: $regular;
    font-size: 16px;
    color: $black;
    border-radius: 5px;
    outline: none;
    &::placeholder {
      line-height: 19px;
      color: $placeholder;
    }
    // disable spin arrows
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
    // disable spin arrows
    &[type="number"] {
      -moz-appearance: textfield;
    }
    &.filled,
    &:focus {
      border-color: $black;
    }
    &.search {
      padding-right: 36px;
      &.filled,
      &:focus {
        background: $surfaceHover;
        border-color: $surfaceHover;
      }
    }
    &.addProduct {
      &.filled,
      &:focus {
        background: transparent;
        border-color: $black;
      }
    }
    &.searchIcon {
      padding-right: 50px;
    }
    &:disabled,
    &:read-only {
      &,
      &:focus {
        border-color: $grayLighter;
        background: $disabled;
      }
    }
  }
  &__msg {
    font-size: 12px;
    line-height: 15px;
    color: $error;
    position: absolute;
    bottom: -17px;
    left: 0;
  }
  &-icon {
    position: absolute;
    top: 7px;
    right: 10px;
    &.clear {
      top: 9px;
      opacity: 0.4;
      cursor: pointer;
      &:hover {
        opacity: 1;
      }
    }
  }
  &-error {
    input {
      &,
      &:focus,
      &.filled {
        border-color: $error;
      }
    }
  }
  &-tableInput {
    height: 100%;
    position: relative;
    input {
      height: 100%;
      padding: 0;
      border: none;
      border-bottom: 1px solid transparent;
      border-radius: 0;
      background: transparent;
      &.filled,
      &:focus {
        background: transparent;
        border-color: transparent;
      }
      &:focus {
        border-color: $black;
      }
    }
  }
  .search-suggestion {
    width: 100%;
    max-height: 235px;
    height: max-content;
    position: absolute;
    top: 40px;
    left: 0;
    background: $white;
    box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 8px 0;
    overflow: hidden;
    overflow-y: auto;
    z-index: 9;
    transition: 0.3s ease;

    &::-webkit-scrollbar {
      width: 17px;
    }

    &::-webkit-scrollbar-thumb {
      background: $placeholder;
      border: 6px solid $white;
      border-radius: 150px;
    }

    span {
      display: flex;
      align-items: center;
      padding: 8.5px 15px;
      line-break: anywhere;
      cursor: pointer;
      transition: 0.2s ease;
      &:hover {
        background: $grayHover;
      }
    }
  }
  &-row {
    width: 563px;
    display: flex;
    align-items: center;
    label {
      width: calc(100% - 380px);
      display: block;
      font-family: $regular;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
    }
    input {
      width: 380px;
      &.short {
        width: 99px;
      }
    }
  }
}
@media (max-width: 992px) {
  .base-input {
    .search-suggestion {
      width: 100% !important;
    }
    input {
      padding-left: 8px;
    }
    &-row {
      width: 100%;
      flex-direction: column;
      align-items: flex-start;
      label {
        width: 100%;
        margin-bottom: 7px;
        transform: translate(0) !important;
      }
      .input,
      input {
        width: 100%;
      }
    }
    &-tableInput {
      input {
        padding-left: 0;
      }
    }
  }
}
</style>
